@font-face {
    font-family: "IBM VGA9+";
    src: url("../../assets/fonts/PxPlus_IBM_VGA9.woff"),
        url("../../assets/fonts/PxPlus_IBM_VGA9.ttf");
}

.terminal-input-container {
    display: flex;
    flex-flow: row;
}

.terminal-input {
    width: 100%;
    background: rgba(0, 0, 0, 0);
    border: 0px;
    outline: none;
    padding: 2px;
    flex-direction: row;
    flex-grow: 1;
    flex-shrink: 1;
    box-sizing: border-box;
    margin: 0px;
}

.terminal-prompt {
    flex-shrink: 1;
    flex-grow: 1;
    flex-direction: row;
    width: fit-content;
    box-sizing: border-box;
    margin-right: 7px;
    margin-top: 1px;
}

.terminal-input:focus {
    outline: none;
}

.terminal-text {
    font-family: "IBM VGA9+", monospace;
    font-size: 16px;
    color: white;
}

.terminal-output {
    width: 100%;
    overflow-y: auto;
    word-wrap: break-word;
    white-space: pre;
    text-align: left;
    flex-direction: column;
    flex-grow: 1;
}

.terminal-container {
    width: 100%;
    height: 100%;
    min-height: 100%;
    display: flex;
    flex-flow: column;
    flex-grow: 1;
    box-sizing: border-box;
    padding: 10px;
}
